import React, { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

const LoginPage = () => {
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    //    URL: ?token=abc123
    const searchParams = new URLSearchParams(location.search);
    const token = searchParams.get('token');

    if (!token) {
      alert('    ');
      navigate('/');
      return;
    }

    //  tokens.json
    fetch('../public/tokens.json')
      .then(res => {
        if (!res.ok) {
          throw new Error('    tokens.json');
        }
        return res.json();
      })
      .then(tokens => {
        //    
        const found = tokens.find(item => item.token === token);

        if (found) {
          //  telegram_id  localStorage
          localStorage.setItem('user', JSON.stringify({ telegramId: found.telegram_id }));
          navigate('/profile'); //  '/', '/dashboard'   
        } else {
          alert('   ');
          navigate('/');
        }
      })
      .catch(err => {
        console.error('   :', err);
        alert('   .  .');
        navigate('/');
      });
  }, [navigate, location]);

  return (
    <div className="login-page">
      <h2></h2>
      <p> ,    /start:</p>
      <a href="https://t.me/runwayshopbot">    </a>
    </div>
  );
};

export default LoginPage;